html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.hannah-landing {
  width: 100%;
  height: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.hannah-landing-section {
  width: 33.333%;
  height: 100%;
  overflow: hidden;
}

.hannah-landing-section-inner {
  background: no-repeat top;
  background-size: cover;
  display: inline-block;
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 0.2s ease-in-out;
  transition: -webkit-transform 0.2s ease-in-out;
  -o-transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out;
  transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.hannah-landing-section-inner:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.hannah-landing-section-inner-content {
  position: relative;
  top: 50%;
  margin: 0;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
  color: white;
}

.hannah-landing-section-inner-content h1, .hannah-landing-section-inner-content p {
  margin: 0;
}

.hannah-landing-section-inner-content h1 {
  text-align: center;
  font-size: 3em;
  text-shadow: 2px 2px black;
}

.hannah-landing-section-inner-content p {
  display: inline-block;
  position: relative;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  background-color: rgba(0, 0, 0, 0.6);
  padding: 0.2em 0.3em;
  font-size: 1.6em;
  border-radius: 5px;
}

@media screen and (max-width: 960px) {
  .hannah-landing {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .hannah-landing-section {
    width: 100%;
    height: 33.333%;
  }
}



